:root {
  --primary: #f26722;
  --blue: #17407e;
  --teal: #027b8a;
  --dark: #171725;
  --light: #f8faff;
  --white: #fff;
  --nav-hover: #60a5fa;
  --nav-border: #374151;
}

*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Segoe UI', Arial, sans-serif;
  background: var(--light);
  color: var(--dark);
  line-height: 1.6;
}

.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

/* === HEADER === */
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background: var(--white);
  position: sticky;
  top: 0;
  z-index: 1000;
  flex-wrap: wrap;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}

/* Logo container */
header .logo {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  white-space: nowrap;
}

header .logo img {
  height: 60px;
  width: auto;
  flex-shrink: 0;
}

header .logo-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* Company name text */
header .company-name {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--primary);
  line-height: 1.2;
  margin: 0;
}

/* Tagline text */
header .tagline {
  font-size: 1rem;
  color: var(--teal);
  font-weight: 500;
  margin: 0;
}

/* Navigation */
header nav {
  display: flex;
  gap: 2rem;
  transition: max-height 0.4s ease, opacity 0.4s ease;
}
header nav a {
  text-decoration: none;
  color: var(--primary);
  font-weight: 700; /* BOLD */
  font-size: 1.2rem; /* INCREASED FONT SIZE */
  padding: 0.5rem 0.75rem;
  transition: color 0.3s ease, border-bottom 0.3s ease;
}
header nav a:hover {
  color: var(--blue);
  border-bottom: 2px solid var(--primary);
}
header nav a.active {
  border-bottom: 2px solid var(--primary);
  color: var(--blue);
}

/* === HERO SECTION === */
.hero {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 4rem 2rem;
  background: linear-gradient(90deg, var(--blue), var(--teal));
  color: var(--white);
  gap: 2rem;
}
.hero-content {
  max-width: 700px;
}
.hero-content h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
}
.hero-content p {
  font-size: 1.3rem;
  margin-bottom: 2rem;
  color: #e5e7eb;
}
.cta-btn {
  background: var(--primary);
  padding: 0.75rem 1.5rem;
  border-radius: 30px;
  text-decoration: none;
  color: var(--white);
  font-weight: 600;
  transition: background 0.3s ease;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
}
.cta-btn:hover {
  background: #ff7f3e;
}
.hero-image img {
  max-width: 450px;
  width: 100%;
}

/* Buttons */
.secondary-btn {
  background: var(--teal);
  color: var(--white);
  padding: 0.6rem 1.5rem;
  border-radius: 30px;
  text-decoration: none;
  font-weight: 600;
  display: inline-block;
  transition: background 0.3s ease;
}
.secondary-btn:hover {
  background: var(--primary);
}

h1,h2,h3,h4,h5,h6 {
  font-family: 'Segoe UI', Arial, sans-serif;
}
h2 {
  color: var(--blue);
  font-size: 1.8rem;
  font-weight: bold;
  margin-top: 2rem;
}
h3 {
  color: var(--primary);
  font-size: 1.5rem;
  margin-top: 1.5rem;
}
h4 {
  color: var(--teal);
  font-size: 1.2rem;
  margin-top: 1rem;
}
h5 {
  color: var(--blue);
  font-size: 1rem;
}
h6 {
  color: var(--primary);
  font-size: 0.95rem;
  font-weight: 600;
}
p {
  margin: 1rem 0;
  font-size: 1rem;
  color: var(--dark);
}

/* Buttons */
button, input[type="submit"], button[type="submit"] {
  background: var(--primary);
  color: var(--white);
  border: none;
  padding: 0.75rem 2.2rem;
  border-radius: 30px;
  font-size: 1rem;
  cursor: pointer;
  font-weight: 600;
  transition: background 0.2s;
  box-shadow: 0 2px 8px rgba(242,103,34,0.12);
}
button:hover, input[type="submit"]:hover, button[type="submit"]:hover {
  background: var(--blue);
}
button.secondary {
  background: var(--teal);
  color: var(--white);
}
button.secondary:hover {
  background: var(--primary);
}

/* Sections */
.services-section, .contact-section, .intro, .quick-services {
  padding: 4rem 2rem;
  text-align: center;
}
.section-title, .quick-services h2 {
  font-size: 2rem;
  color: var(--blue);
  font-weight: bold;
  margin-bottom: 2.5rem;
}

/* === SERVICES GRID (3 in first row + 2 centered in second row) === */
.services-grid {
  display: grid;
  grid-template-columns: repeat(3, 300px); /* fixed width cards */
  justify-content: center; /* center grid as a whole */
  gap: 2rem;
}
.services-grid .service-card {
  width: 300px;
}
.service-card, .service-preview {
  background: var(--white);
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.05);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-align: left;
}
.service-card:hover, .service-preview:hover {
  transform: translateY(-6px);
  box-shadow: 0 6px 16px rgba(0,0,0,0.1);
}

/* === QUICK SERVICES GRID === */
.quick-services-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin-bottom: 2rem;
}
.quick-services-grid .service-preview {
  flex: 1 1 calc(33.333% - 2rem);
  max-width: 300px;
}
@media (max-width: 900px) {
  .quick-services-grid .service-preview {
    flex: 1 1 calc(50% - 2rem);
  }
}
@media (max-width: 600px) {
  .quick-services-grid .service-preview {
    flex: 1 1 100%;
  }
}

/* Contact Form */
.contact-form-section {
  background: var(--white);
  border-radius: 10px;
  padding: 2rem;
  margin-top: 2rem;
  box-shadow: 0 4px 24px rgba(23,64,126,0.07);
}
.contact-form label {
  display: block;
  margin: 1rem 0 0.3rem;
  font-weight: 600;
  color: var(--blue);
}
.contact-form input, .contact-form textarea {
  width: 100%;
  padding: 0.6rem;
  border: 2px solid var(--blue);
  border-radius: 4px;
  font-size: 1rem;
  background: var(--light);
  color: var(--dark);
}
.contact-form textarea {
  resize: vertical;
  min-height: 100px;
}
.contact-form button {
  width: 50%;
  margin: 0 auto;
}

/* Footer */
footer {
  background: var(--blue);
  color: var(--white);
  text-align: center;
  padding: 2rem 1rem 1rem;
  margin-top: 3rem;
}
.footer-logo img {
  max-width: 120px;
  margin-bottom: 1rem;
}

/* Mobile menu hamburger */
.hamburger {
  display: none;
  flex-direction: column;
  justify-content: center;
  width: 30px;   /* smaller width */
  height: 30px;  /* smaller height */
  cursor: pointer;
  z-index: 1001;
}
.hamburger span {
  height: 5px;   /* thicker/bold lines */
  width: 100%;
  background: var(--primary);
  margin: 6px 0;
  border-radius: 5px;
  transition: all 0.35s;
  display: block;
}
.hamburger.is-active span:nth-child(1) {
  transform: translateY(10px) rotate(45deg);
}
.hamburger.is-active span:nth-child(2) {
  opacity: 0;
}
.hamburger.is-active span:nth-child(3) {
  transform: translateY(-10px) rotate(-45deg);
}

/* === MEDIA QUERIES === */
@media (max-width: 900px) {
  .services-grid {
    grid-template-columns: repeat(2, 300px);
  }
}

@media (max-width: 700px) {
  .hamburger {
    display: flex;
    position: absolute;
    top: 12px;
    right: 18px;
  }
  header nav {
    flex-direction: column;
    position: absolute;
    top: 60px;
    right: 12px;
    background: var(--white);
    padding: 1rem;
    border-radius: 8px;
    box-shadow: 0 6px 18px rgba(23,64,126,0.11);
    max-height: 0;
    opacity: 0;
    overflow: hidden;
  }
  header nav.menu-active {
    max-height: 500px;
    opacity: 1;
  }
  header nav a {
    padding: 0.7rem 0;
    text-align: right;
  }
}

@media (max-width: 600px) {
  .services-grid {
    grid-template-columns: 1fr;
  }
  main {
    max-width: 97vw;
    padding: 0.8rem;
  }
  .contact-form-section {
    padding: 1rem;
  }
}

@media (max-width: 480px) {
  .branding h1 {
    font-size: 1.3rem;
  }
  .tagline {
    font-size: 0.95rem;
  }
}
